<template>
  <div>
    <Width100>
      <Paragraph
        title="Personas"
        paragraph="I created different personas to understand the type of person I was designing for. This helped examined the process and workflow that the users would prefer to utilize to achieve their objectives in interacting with Sumi."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <div class="img-ctr">
      <VueImage :src="persona" alt="Persona" :isExpandable="true" />
    </div>
  </Width100>
  </div>
</template>

<script>
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width70Centered from '../common/layout/Width70Centered';

import Width100 from '../common/layout/Width100';

import persona from "../../assets/img/sumi/persona.png";

export default {
    components: {
      Paragraph,
      VueImage,
      Width70Centered,

      Width100,
    },

    data() {
      return {
        persona,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-ctr {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



