<template>
  <div>
    <Width100 v-bind:addPadding="true">
      <Width50>
        <Width100>
          <Paragraph
          subtitle="What is Sumi?"
          paragraph="An Android app that allows you to select ambience sounds of your choice and help calm your mind for a better night sleep."
          />
        </Width100>
        <Width100>
          <Paragraph subtitle="The Problem" paragraph="Due to constant stress people experience everyday, it's more difficult to get a good night sleep. Modern apps are built with multiple features that enables  people to keep their mind awake when they are attemping to sleep." />
        </Width100>
        <Width100>
          <Paragraph subtitle="The Solution" paragraph="Create a simpler and more efficient way for people to find sounds that helps them sleep better." />
        </Width100>
        <Width100>
          <Paragraph subtitle="The Team" paragraph="I designed this app mysef and was developed by Anthony Wang." />
        </Width100>
        <Width100>
          <Paragraph subtitle="Tools" paragraph="Invision, Sketch" />
        </Width100>
      </Width50>
      <Width50>

        <div class="marvel-container">
          <iframe src="https://marvelapp.com/eh14dgh?emb=1&iosapp=false&frameless=false" title="Prototype" width="100%" height="784" allowTransparency="true" frameborder="0"></iframe>
        </div>
      </Width50>
    </Width100>
  </div>
</template>

<script>
import ProjectHeader from '../common/ProjectHeader';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import UnorderedList from '../common/UnorderedList';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';
import VueImage from '../common/VueImage';

export default {
  components: {
    LineBreak,
    ProjectHeader,
    Paragraph,
    VueImage,
    Width50,
    Width100,
  },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  margin: 32px auto;
}

.unordered_list {
  text-align: left;
}

.marvel-container {
  margin: 0 auto;
  text-align: center;
  border: 0;
}
</style>



