<template>
  <div>
    <Width100>
      <Paragraph
        title="Information Architecture"
        paragraph="The average user would be using the application most likely at night right before going to bed. This emphasizes the importance of simplicity and the usage of dark colors. I needed to make all of the gestures and buttons easily accessible and quick."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <ParagraphWithLeadingTitle title="Skip Login" paragraph="When allowed to skip, users don't have to tediously input their information when they just want to sleep.This will decreases the extra friction for our fatigue users." />
    </Width100>

    <Width100>
      <div class="img-container">
        <VueImage :src="sitemap" alt="Sitemap" :isExpandable="true" />
      </div>
    </Width100>
  </div>
</template>

<script>
import Paragraph from '../common/Paragraph';
import ParagraphWithLeadingTitle from "../common/ParagraphWithLeadingTitle";
import VueImage from '../common/VueImage';
import Width100 from '../common/layout/Width100';

import sitemap from "../../assets/img/sumi/sitemap.png";

export default {
    components: {
      Paragraph,
      ParagraphWithLeadingTitle,
      VueImage,
      Width100,
    },

    data() {
      return {
        sitemap,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}
</style>



