<template>
  <div>
    <Landing />
    <Introduction />
    <ResearchAndUsabilityStudies />
    <Personas />
    <InformationArchitecture />
    <Prototyping />
    <HighFidelityWireframes />
    <PrototypeGif />

    <NextProjectRouter
      :currentIdx="0"
    />
  </div>
</template>

<script>
import Landing from './Landing';
import Introduction from "./Introduction";
import ResearchAndUsabilityStudies from "./ResearchAndUsabilityStudies";
import Personas from "./Personas";
import InformationArchitecture from "./InformationArchitecture";
import Prototyping from "./Prototyping";
import HighFidelityWireframes from "./HighFidelityWireframes";
import PrototypeGif from "./PrototypeGif";

import NextProjectRouter from "../NextProjectRouter";

export default {
    name: 'Sumi',
    components: {
      Landing,
      Introduction,
      ResearchAndUsabilityStudies,
      Personas,
      InformationArchitecture,
      Prototyping,
      HighFidelityWireframes,
      PrototypeGif,
      NextProjectRouter,
    },
    mounted () {
      this.$ga.page({
        page: '/sumi',
        title: 'Sumi',
        location: window.location.href
      });
    },

};
</script>



