<template>
  <div>
    <Width100>
      <Paragraph
      title="High Fidelity Wireframes"
      paragraph="I wanted to make the app feel relaxing and calm. To accomplish this, I used dark colors that promotes sleep such as soft blue, purple, and grey . Since users will be using the app right beofre they sleep, I strictly avoided lights colors to provide a better night experience."
      v-bind:center="true"
      />
    </Width100>
      <Width70Centered>
        <div class="img-container">
          <VueImage :src="high_fidelity" alt="High fidelity Wireframe" :isExpandable="true" />
        </div>
    </Width70Centered>
  </div>
</template>

<script>
import ParagraphWithLeadingTitle from "../common/ParagraphWithLeadingTitle";
import Paragraph from '../common/Paragraph';
import UnorderedList from '../common/UnorderedList';
import Width70Centered from '../common/layout/Width70Centered';
import Width100 from '../common/layout/Width100';
import VueImage from '../common/VueImage';

import high_fidelity from "../../assets/img/sumi/high_fidelity.png";

export default {
  components: {
    Paragraph,
    VueImage,
    Width70Centered,
    Width100,
  },

  data() {
    return {
      high_fidelity,
    }
  }
};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  margin: 32px auto;
}
</style>



