<template>
  <Width100>
    <VueImage :src="banner" alt="Landing banner" :isExpandable="true"  />
  </Width100>
</template>

<script>
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import banner from "../../assets/img/sumi/header.png";

export default {
    name: 'IgnLanding',
    components: {
      VueImage,
      Width50,
      Width100,
    },
    data() {
      return {
        banner
      }
    }
};
</script>

<style lang="scss" scoped>
</style>



