<template>
  <div>
    <Width100>
      <Paragraph
        title="Discovery"
        subtitle="Research"
        paragraph="Researching sleeping behaviors revealed many articles that analyzed how phones impacts the human mind during night time. According to the Harvard study, reading a screen before sleeping will cause you to feel more sleepy and sluggish when you wake up the next day. Looking at screens will trigger your brain to stay awake and you'll be less likely to feel like going to bed."
        v-bind:center="true"
      />
    </Width100>

    <Width100>
      <SourceLink source="Psychology Today" target="_blank" href="https://www.psychologytoday.com/us/blog/prescriptions-life/201804/6-ways-night-time-phone-use-destroys-your-sleep" />
    </Width100>
    <Width100>
      <Paragraph
        :paragraph="article"
        v-bind:center="true"
      />
    </Width100>
    <Width100>
      <SourceLink source="SleepFoundation.org" target="_blank" href="https://www.sleepfoundation.org/bedroom-environment/see/how-electronics-affect-sleep" />
    </Width100>

    <Width100>
      <ParagraphWithLeadingTitle title="Confirming the Need" paragraph="I started by informally surveying friends and families regarding their sleeping routine, and 30 out of 30 of them have experience sleeping issues due to high anxiety or stress at work." />
    </Width100>

    <Width70Centered>
      <Width50>
        <UnorderedList
        title="Observations"
        v-bind:items="observationDataPoints"
        />
      </Width50>
      <Width50>
        <UnorderedList
        title="Key Insights"
        v-bind:items="keyInsightsDataPoints"
        />
      </Width50>
    </Width70Centered>
    <Width100>
      <div class="img-container">
        <VueImage :src="graph" alt="Observations" :isExpandable="true" />
      </div>
    </Width100>
  </div>
</template>


<script>
import ParagraphWithLeadingTitle from "../common/ParagraphWithLeadingTitle";
import CenteredText from '../common/CenteredText';
import AnchorButton from '../common/AnchorButton';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width70Centered from '../common/layout/Width70Centered';
import Width100 from '../common/layout/Width100';
import VueImage from '../common/VueImage';
import UnorderedList from '../common/UnorderedList';
import SourceLink from '../common/SourceLink';

import graph from "../../assets/img/sumi/graph.png";

export default {
    components: {
      AnchorButton,
      CenteredText,
      Paragraph,
      VueImage,
      Width50,
      Width70Centered,
      Width100,
      UnorderedList,
      ParagraphWithLeadingTitle,
      SourceLink,
    },
    data() {
      return {
        graph,
        article: `In an article documenting "How Electronics Affects Sleep" they specifically outline: "Light from electronics has the potential to disrupt sleep because it sends alerting signals to the brain. Blue light, a type of life which the brain interprets as daylight, makes you feel awake past bedtime."`,
        observationDataPoints: [
          "Twenty-three out of thirty of participants said they downloaded apps to help them monitor their sleeping pattern, but has not improved their sleeping.",
          "Seven of thirty said they meditate or exercise to clear their mind before going to bed.",
          "Eighteen out of thirty of them prefer to sleep with background noises such as fans.",
          "Twelve out of thirty of them prefer to sleep with absolute silence",
        ],
        keyInsightsDataPoints: [
          "The longer phone screen time, the higher chance people will have problems falling asleep at a reasonable time.",
          "People who prefer white noise is often because sleeping  with complete silence is unsettling.",
          "Although majority of our participant prefer white noise,  we cannot ultimately determine if it will help people sleep due to how complex the human mind is.",
        ],
      };
    },
};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  max-width: 720px;
  margin: 32px auto;
}

.unordered_list {
  text-align: left;
  padding: 0;
}

</style>



