<template>
  <div>
    <Width100>
      <Paragraph
      title="Prototyping & Testing"
      subtitle="Wireframes"
      v-bind:center="true"
      />
    </Width100>
  </Width100>
  <Width100>
    <ParagraphWithLeadingTitle title="Initial Sketches" paragraph="Based on my reseach, the longer users look at their phone screen, the harder it will be to fall asleep. To help make the process of finding sounds faster, I really wanted the applications overall design to feel very minimalist as possible. If designed with too much content, it could seem distracting and keep users starting at their screen. I narrowed it down to two options:" />
  </Width100>
  <Width70Centered>
    <UnorderedList
    v-bind:items="concepts"
    />
  </Width70Centered>

  <Width70Centered>
    <div class="img-container">
      <VueImage :src="concept" alt="Wireframe" :isExpandable="true" />
    </div>
  </Width70Centered>

  <Width100>
    <Paragraph
    paragraph="I chose Concept 2 because I learned through user testing that users would like to see their options as quick as possible in order to not spend too much screen time on the app searching."
    v-bind:center="true"
    />
  </Width100>
  <Width100>
    <Paragraph
    paragraph="However, with the new findings through mock tests, I tweaked the design in Concept 2 and landed on Concept 3 to further simplify interactions."
    v-bind:center="true"
    />
  </Width100>
  <Width100>
    <ParagraphWithLeadingTitle title="Concept 3" paragraph="Homescreen is the music player. Users can directly search for songs, favorite, and edit the duration. Users will find sounds faster and not stare at the screen for too long. Users can favorite songs and find it on the first tab for quick access." />
  </Width100>
  <Width70Centered>
    <div class="img-container">
      <VueImage :src="concept_3" alt="Wireframe" :isExpandable="true" />
    </div>
  </Width70Centered>
</div>
</template>

<script>
import ParagraphWithLeadingTitle from "../common/ParagraphWithLeadingTitle";
import Paragraph from '../common/Paragraph';
import UnorderedList from '../common/UnorderedList';
import Width70Centered from '../common/layout/Width70Centered';
import Width100 from '../common/layout/Width100';
import VueImage from '../common/VueImage';

import concept from "../../assets/img/sumi/Group_12.png";
import concept_3 from "../../assets/img/sumi/concept_3.png";

export default {
  components: {
    Paragraph,
    ParagraphWithLeadingTitle,
    VueImage,
    UnorderedList,
    Width70Centered,
    Width100,
  },

  data() {
    return {
      concept,
      concept_3,
      concepts: [
        "Concept 1: Homescreen begins with a categories",
        "Concept 2: Homescreen begins with categories with relative sounds",
      ]
    }
  }
};
</script>

<style lang="scss" scoped>
.img-container {
  padding: 0 32px;
  margin: 32px auto;
}

.unordered_list {
  text-align: left;
}
</style>



