<template>
  <a
    :href="href"
    :target="target">
    Source: <b>{{source}}</b>
  </a>
</template>

<script>
export default {
  props: {
    href: {
      type: String,
      default: "",
    },
    source: {
        type: String,
        default: "",
    },
    target: {
      type: String,
      default: "",
    },
  },
};
</script>

<style lang="scss" scoped>
a {
  color: #e76d89;

  width: 70%;
  margin: 16px auto;

  @media screen and (max-width: 560px) {
    width: 100%;
    display: block;
  }
  @media screen and (max-width: 900px) {
    padding: 0 32px;
  }
}
</style>



