<template>
  <div class="paragraph-container">
    <p class="paragraph">
        <b>{{title}}: </b>{{paragraph}}
    </p>
  </div>
</template>

<script>
    export default {
      name: 'ParagraphWithLeadingTitle',
      props: {
        title: String,
        paragraph: String,
      }
    }
</script>

<style lang="scss" scoped>
.paragraph-container {
  .paragraph {
    line-height: 2;
    text-align: left;
    width: 70%;
    margin: 0 auto;
  }

  @media screen and (max-width: 560px) {
    .paragraph {
      width: 100%;
    }
  }
  @media screen and (max-width: 900px) {
    padding: 0 32px;
  }
}
</style>



