<template>
  <div>
    <Width100>
      <Paragraph
        subtitle="Prototyping"
        v-bind:center="true"
      />
    </Width100>

    <Width70Centered>
      <div class="img-ctr">
        <VueImage :src="Sumi_Prototype" alt="Prototype gif" :isExpandable="true" />
      </div>
    </Width70Centered>
  </div>
</template>

<script>
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width70Centered from '../common/layout/Width70Centered';

import Width100 from '../common/layout/Width100';

import Sumi_Prototype from "../../assets/img/sumi/Sumi_Prototype.gif";

export default {
    components: {
      Paragraph,
      VueImage,
      Width70Centered,
      Width100,
    },

    data() {
      return {
        Sumi_Prototype,
      };
    },

};
</script>

<style lang="scss" scoped>
.img-ctr {
  margin: 16px 0;
}
</style>



