<template>
<div class="unordered_list">
  <h3 class="title" v-if="title" :style="titleStyles">{{title}}</h3>
  <ul>
    <li class="list_item" v-for="item in items">
      {{item}}
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: 'UnorderedList',
  props: {
    items: {
      type: Array,
    },
    title: {
      type: String,
    },
    titleColor: {
      type: String,
    },
  },
  computed: {
    titleStyles: function() {
      return {
        color: this.titleColor ? this.titleColor: 'black',
      };
    }
  }
};
</script>

<style lang="scss" scoped>
.unordered_list {
  padding: 32px;

  @media screen and (max-width: 560px) {
  padding: 16px 32px;
  }
}

.title {
  padding: 20px 0;
}
.list_item {
  margin-bottom: 16px;
  line-height: 2;
}

@media screen and (max-width: 900px) {
  .unordered_list {
    .title {
      text-align: center;
    }
  }
}
</style>



